<template>
  <div>
    <reloadAndsearch ref="search" :config="searchConfig" @search="search" />
    <leftCard ref="leftcard" :categoryType="categoryType" :title="title" style="margin-top:0;height:600px" />
    <div class="list-model">
      <TableList :pageMethod="getTableData" :searchMethod="getTableData" :table-data="tableData"
        :tableColumn="tableColumn" :query.sync="query" :total="total" :loading="loadings.table" :multiCheck="multiCheck">
        <!-- 1A-1B-1C-1D-1E-', -->
        <template v-slot:column-cooperation="props">
          <span>{{ props.row.cooperationType == '1' ? ''
            : (props.row.cooperationType == '2' ? ''
              : (props.row.cooperationType == '3' ? ''
                : (props.row.cooperationType == '4' ? ''
                  : (props.row.cooperationType == '5' ? '' : '-'))))
          }}</span>
        </template>
        <!-- 3A-3B-3C-3D-3E-3F-',   -->
        <template v-slot:column-status="props">
          <span>{{ props.row.shopStatus == '1' ? ''
            : (props.row.shopStatus == '2' ? ''
              : (props.row.shopStatus == '3' ? ''
                : (props.row.shopStatus == '4' ? ''
                  : (props.row.shopStatus == '5' ? ''
                    : (props.row.shopStatus == '6' ? '' : '-')))))
          }}</span>
        </template>
        <template v-slot:column-todo="props">
          <el-button class="prohibitclick" @click="editRow(props.row)" type="text" size="small"></el-button>
          <el-button class="prohibitclick" @click="deleteRow(props.row)" type="text" size="small"></el-button>
        </template>
      </TableList>
      <shopEdit ref="shopEdit" v-if="shopEditVisable" :dialogVisible="shopEditVisable" :rowData="rowData"
        @close="shopEditVisable = false" @success="_success()"></shopEdit>
    </div>
  </div>
</template>

<script>
import shopEdit from "./shopEdit.vue";
import leftCard from "@/components/public/leftCard.vue";
import { shoplistPage, shopDelete, shopDeleteList } from "@/api/data";
import TableList from "@/components/public/tableList";
import reloadAndsearch from "@/components/public/reloadAndsearch/reloadAndsearch.vue";

export default {
  name: "shopList",
  data() {
    return {
      total: null,
      rowData: {},
      shopEditVisable: false,
      multiCheck: true,
      title: "",
      categoryType: "SHOP",
      list: [],
      hidden: true,
      inputCategory: "",
      tableData: [],
      multipleSelection: [],
      strDelete: '',
      loadings: {
        table: true,
      },
      query: {
        pageNo: 1,
        pageSize: 10,
      },
      shopStatusList: [
        { label: "", value: "1" },
        { label: "", value: "2" },
        { label: "", value: "3" },
        { label: "", value: "4" },
        { label: "", value: "5" },
        { label: "", value: "6" }
      ],
      cooperationTypeList: [
        { label: "", value: "1" },
        { label: "", value: "2" },
        { label: "", value: "3" },
        { label: "", value: "4" },
        { label: "", value: "5" }
      ]
    };
  },
  props: {},
  components: {
    shopEdit,
    leftCard,
    TableList,
    reloadAndsearch
  },
  created() {
    // this.getTableData()
  },
  computed: {
    tableColumn() {
      return [
        { prop: "shopCode", label: "" },
        { prop: "shopName", label: "" },
        { slots: { name: "column-cooperation" }, label: "", width: "100px" },//1A-1B-1C-1D-1E-',
        { slots: { name: "column-status" }, label: "", width: "100px" },//3A-3B-3C-3D-3E-3F-',   
        { prop: "businessPersonCode", label: "" },
        { prop: "liablePersonCode", label: "" },
        { prop: "province", label: "", width: "100px" },
        { prop: "city", label: "", width: "100px" },
        { prop: "district", label: "", width: "100px" },
        { prop: "detail", label: "" },
        { prop: "description", label: "" },
        { slots: { name: "column-todo" }, label: "", fixed: "right" },
      ]
    },
    searchConfig() {
      return [
        {
          label: '',
          placeholder: '',
          field: 'shopName',
          value: '',
          type: 'input'
        },
        {
          label: '',
          placeholder: '',
          field: 'shopStatus',
          value: '',
          type: "select",
          options: this.shopStatusList
        },
        {
          label: '',
          placeholder: '',
          field: 'cooperationType',
          value: '',
          type: "select",
          options: this.cooperationTypeList
        }
      ];
    }
  },
  methods: {
    getTableData(pageNo = 1, pageSize) {
      this.query.pageNo = pageNo || 1;
      if (pageSize) {
        this.query.pageSize = pageSize;
      }
      this.loadings.table = true;

      let params = {
        // ...this.query,
        page: this.query.pageNo,
        size: this.query.pageSize,
      };
      shoplistPage(params).then((res) => {
        if (res.data.code == 200) {
          this.total = res.data.data.total;
          this.tableData = res.data.data.records;
          // console.log(this.total, this.tableData);
        } else {
          console.log("error");
        }
      })
        .finally(() => {
          this.loadings.table = false;
        });
    },
    deleteRow(row) {
      // console.log("deleteRow", row)
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        shopDelete({ shopKey: row.shopKey }).then(res => {
          if (res.data.code == 200) {
            this.$message.success("!");
            this.getTableData()
            this.$forceUpdate()
          } else {
            this.$message.error("!");
          }
        });

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    //
    handleSelectionDelete(val) {
      this.multipleSelection = val
    },
    handleDeleteList() {
      if (this.multipleSelection.length > 0) {
        let shopKeys = [];
        this.multipleSelection.forEach(item => {
          shopKeys.push({ shopKey: item.shopKey })
        })
        console.log(shopKeys);
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          shopDeleteList(shopKeys).then(() => {
            this.getTableData();
            this.$message({
              type: 'success',
              message: '!'
            });
            this._handleFresh()
          }).catch(error => {
            console.log(error);
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
          this.multipleSelection = []
        });
      } else {
        this.$message({
          type: 'error',
          message: ''
        });
      }
    },
    reload() {
      this.getTableData()
      this.$refs.leftcard.getTree()
    },
    add() {
      this.editRow({})
    },
    editRow(row) {
      this.rowData = row;
      this.shopEditVisable = true;
    },
    handleClose(done) {
      done();
    },
    search(pageNo = 1, pageSize) {
      this.query.pageNo = pageNo;
      if (pageSize) {
        this.query.pageSize = pageSize;
      }
      const searchData = this.$refs.search.search
      shoplistPage({
        ...searchData,
        categoryKey: this.inputCategory,
        page: this.query.pageNo,
        size: this.query.pageSize,
      }).then((res) => {
        this.tableData = res.data.data.records;
        this.total = res.data.data.total;
        this.$forceUpdate()
      })
        .finally(() => {
          this.loadings.table = false;
        });
    },
    success() {
      this.rowData = {};
      this.shopEditVisable = false
      this.getTableData()
    },
    cleanInput() {
      this.$refs.leftcard.isActive = ""
      this.inputCategory = ''
    }
  },
};
</script>
<style lang="scss" scoped>
.page-box {
  text-align: right;
  margin: 20px;
}

.list-model {
  width: 100%;
}

.el-table {
  overflow-y: auto !important;
  overflow-x: auto !important;
}

.el-table__fixed {
  height: auto !important; // !importantele-ui
  bottom: 17px; //     position: absolute;top: 0;left: 0;bottom
}

.list-model {
  margin-top: 20px;
}
</style>
