import http from '@/utils/http'
import qs from "qs";
import request from "../utils/request";

/**
 *  @parms resquest  http://197.82.15.15:8088/request/...
 *  @param '/testIp'vue-cilconfigindex.js
 */
let baseURL = "/api"

// 
export function UserList(params) {
  return request({
    url: baseURL + "/user/list",
    method: "get",
    params: qs.parse(params),
  });
}

// 
export function getUserList(params) {
  return request({
    url: baseURL + "/user/selectUserPage",
    method: "get",
    params: qs.parse(params),
  });
}

//
export function createUser(data) {
  return request({
    url: baseURL + "/user/createUser",
    method: "post",
    data: qs.parse(data),
  });
}

// userId 
export function deleteUserById(data) {
  return request({
    url: baseURL + "/user/deleteUserById",
    method: "get",
    params: qs.parse(data),
  });
}

// userId 
export function deleteUserByIdList(data) {
  return request({
    url: baseURL + "/user/deleteUserByIdList",
    method: "get",
    params: qs.parse(data),
  });
}

//userId
export function updateUserById(data) {
  return request({
    url: baseURL + "/user/updateUserById",
    method: "post",
    data: qs.parse(data),
  });
}